.container2 {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: none; //flex
  justify-content: center;
  align-items: center; }

.menu {
  display: flex;
  flex-grow: 1;
  height: 72px;
  max-width: 370px;
  border-radius: 10px;
  overflow: hidden;
  background-color: #fff;
  margin: 2%;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px; }

.menu-item {
  display: flex;
  text-decoration: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1; }

.menu-item:focus, .menu-item:hover {
  background-color: #fe5e2b;
  color: #fff;
  margin: 2%;
  border-radius: 5px; }

.menu-item:focus .material-icons, .menu-item:hover .material-icons {
  font-family: "Material Icons";
  color: #fff; }

.menu-item:focus .menu-item-label, .menu-item:hover .menu-item-label {
  color: #fff; }

.material-icons {
  font-family: "Material Icons";
  display: block;
  margin-bottom: -4px;
  font-size: 26px;
  color: #fe5e2b;
  transition: 0.25s ease; }

.menu-item-label {
  display: block;
  font-size: 13px;
  color: #999;
  transition: 0.25s ease; }









.grid-view {
  transform: scale(0.75); }

.block-wrap {
  width: 460px;
  height: 360px;
  display: flex;
  position: relative;
  overflow: hidden;
  justify-content: center;
  border-radius: 8px;
  margin: auto; }

.block-col {
  display: flex;
  height: 100%;
  margin: 0 12px;
  width: 124px;
  flex-shrink: 0; }

.block {
  height: 124px;
  width: 100%;
  border-radius: 8px;
  margin: auto 0;
  position: relative;
  will-change: transform;

  &:not(.block--active) {
    cursor: pointer;

    &:hover, &.fake-hover {
      transform: translateY(-12px); }

    &:hover:after, &.fake-hover:after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      transform: scale(1.1) translateY(12px); }

    &:active {
      transform: scale(0.9) translateY(-12px); } } }

.block--active {
  position: absolute;
  left: 0;
  z-index: 1;
  height: 100%;
  width: 100%; }

.block--transition {
  transition: transform 0.185s cubic-bezier(0.4, 0, 0, 1); }

.block-content {
  display: none;
  padding: 24px; }

.block--active .block-content {
  display: block; }

.block-content__header {
  background: rgba(0, 0, 0, 0.1);
  padding: 24px;
  margin: -24px -24px 24px;
  overflow: hidden; }

.block-content__header__text {
  will-change: transform;
  margin: 0;
  opacity: 0;
  font-size: 2em; }

.block--active .block-content__header__text {
  animation: content-in 0.225s cubic-bezier(0, 0, 0.2, 1) forwards;
  animation-delay: 0.15s; }

.block-content__body {
  will-change: transform;
  opacity: 0;
  font-size: 18px;
  line-height: 1.333;
  animation: content-in 0.245s cubic-bezier(0, 0, 0.2, 1) forwards;
  animation-delay: 0.1s;
  margin: 0 0 20px; }

.block-content__button {
  font-weight: bold;
  background: rgba(255, 255, 255, 0.25);
  display: inline-block;
  padding: 16px;
  position: relative;
  left: 150px;
  border-radius: 4px;
  color: rgba(255, 255, 255, 0.85);
  animation: button-in 0.245s cubic-bezier(0, 0, 0.2, 1) forwards;
  animation-delay: 0.2s;
  opacity: 0;
  cursor: pointer; }

@keyframes content-in {
  0% {
    opacity: 0;
    transform: translateY(128px); }

  100% {
    opacity: 1;
    transform: translateY(0); } }

@keyframes button-in {
  0% {
    opacity: 0;
    transform: translateY(64px); }

  100% {
    opacity: 1;
    transform: translateY(0); } }

