  &.block--active {
    background: #66d966; }

  .block-content__header {
    background: #008000; }

  .block-content__button {
    background: #008000;
    border-bottom: 2px solid #004d00;
    border-top: 2px solid #00b300; } }



.block-img {
  display: block;
  margin: 10% auto auto; }





//Sign-In & Sign-Up
.container {
  position: relative;
  width: 100%;
  background-color: #fff;
  min-height: 100vh;
  overflow: hidden; }

.forms-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0; }

.signin-signup {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  left: 75%;
  width: 50%;
  transition: 1s 0.7s ease-in-out;
  display: grid;
  grid-template-columns: 1fr;
  z-index: 5; }

form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0rem 5rem;
  transition: all 0.2s 0.7s;
  overflow: hidden;
  grid-column: 1 / 2;
  grid-row: 1 / 2;

  &.sign-up-form {
    opacity: 0;
    z-index: 1; }

  &.sign-in-form {
    z-index: 2; } }

.title {
  font-size: 2.2rem;
  color: #444;
  margin-bottom: 10px; }

.input-field {
  max-width: 380px;
  width: 100%;
  background-color: #f0f0f0;
  margin: 10px 0;
  height: 55px;
  border-radius: 55px;
  display: grid;
  grid-template-columns: 15% 85%;
  padding: 0 0.4rem;
  position: relative;

  i {
    text-align: center;
    line-height: 55px;
    color: #acacac;
    transition: 0.5s;
    font-size: 1.1rem; }

  input {
    background: none;
    outline: none;
    border: none;
    line-height: 1;
    font-weight: 600;
    font-size: 1.1rem;
    color: #333;

    &::placeholder {
      color: #aaa;
      font-weight: 500; } } }

.social-text {
  padding: 0.7rem 0;
  font-size: 1rem; }

.social-media {
  display: flex;
  justify-content: center; }

.social-icon {
  height: 46px;
  width: 46px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 0.45rem;
  color: #333;
  border-radius: 50%;
  border: 1px solid #333;
  text-decoration: none;
  font-size: 1.1rem;
  transition: 0.3s;

  &:hover {
    color: #4481eb;
    border-color: #4481eb; } }

.btn {
  width: 150px;
  background-color: #5995fd;
  border: none;
  outline: none;
  height: 49px;
  border-radius: 49px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 600;
  margin: 10px 0;
  cursor: pointer;
  transition: 0.5s;

  &:hover {
    background-color: #4d84e2; } }

.panels-container {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr); }

.container:before {
  content: "";
  position: absolute;
  height: 2000px;
  width: 2000px;
  top: -10%;
  right: 48%;
  transform: translateY(-50%);
  background-image: linear-gradient(-45deg, #4481eb 0%, #04befe 100%);
  transition: 1.8s ease-in-out;
  border-radius: 50%;
  z-index: 6; }

.animated {
  width: 100%;
  transition: transform 1.1s ease-in-out;
  transition-delay: 0.4s; }

.panel {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-around;
  text-align: center;
  z-index: 6; }

.left-panel {
  pointer-events: all;
  padding: 3rem 17% 2rem 12%; }

.right-panel {
  pointer-events: none;
  padding: 3rem 12% 2rem 17%; }

.panel {
  .content {
    color: #fff;
    transition: transform 0.9s ease-in-out;
    transition-delay: 0.6s; }

  h3 {
    font-weight: 600;
    line-height: 1;
    font-size: 1.5rem; }

  p {
    font-size: 0.95rem;
    padding: 0.7rem 0; } }

.btn.transparent {
  margin: 0;
  background: none;
  border: 2px solid #fff;
  width: 130px;
  height: 41px;
  font-weight: 600;
  font-size: 0.8rem; }

.right-panel {
  .animated, .content {
    transform: translateX(800px); } }

/* ANIMATION */

.container.sign-up-mode {
  &:before {
    transform: translate(100%, -50%);
    right: 52%; }

  .left-panel {
    .animated, .content {
      transform: translateX(-800px); } }

  .signin-signup {
    left: 25%; }

  form {
    &.sign-up-form {
      opacity: 1;
      z-index: 2; }

    &.sign-in-form {
      opacity: 0;
      z-index: 1; } }

  .right-panel {
    .animated, .content {
      transform: translateX(0%); } }

  .left-panel {
    pointer-events: none; }

  .right-panel {
    pointer-events: all; } }











@media (max-width: 870px) {
  .container {
    min-height: 800px;
    height: 100vh; }

  .signin-signup {
    width: 100%;
    top: 95%;
    transform: translate(-50%, -100%);
    transition: 1s 0.8s ease-in-out;
    left: 50%; }

  .container.sign-up-mode .signin-signup {
    left: 50%; }

  .panels-container {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 2fr 1fr; }

  .panel {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 2.5rem 8%;
    grid-column: 1 / 2; }

  .right-panel {
    grid-row: 3 / 4; }

  .left-panel {
    grid-row: 1 / 2; }

  .animated {
    width: 200px;
    transition: transform 0.9s ease-in-out;
    transition-delay: 0.6s; }

  .panel {
    .content {
      padding-right: 15%;
      transition: transform 0.9s ease-in-out;
      transition-delay: 0.8s; }

    h3 {
      font-size: 1.2rem; }

    p {
      font-size: 0.7rem;
      padding: 0.5rem 0; } }

  .btn.transparent {
    width: 110px;
    height: 35px;
    font-size: 0.7rem; }

  .container {
    &:before {
      width: 1500px;
      height: 1500px;
      transform: translateX(-50%);
      left: 30%;
      bottom: 68%;
      right: initial;
      top: initial;
      transition: 2s ease-in-out; }

    &.sign-up-mode {
      &:before {
        transform: translate(-50%, 100%);
        bottom: 32%;
        right: initial; }

      .left-panel {
        .animated, .content {
          transform: translateY(-300px); } }

      .right-panel {
        .animated, .content {
          transform: translateY(0px); } } } }

  .right-panel {
    .animated, .content {
      transform: translateY(300px); } }

  .container.sign-up-mode .signin-signup {
    top: 5%;
    transform: translate(-50%, 0); } }




@media (max-width: 570px) {

  form {
    padding: 0 1.5rem; }

  .animated {
    display: none; }


  .animated {
    display: none; }

  .panel .content {
    padding: 0.5rem 1rem; }

  .container {
    padding: 1.5rem;

    &:before {
      bottom: 72%;
      left: 50%; }

    &.sign-up-mode:before {
      bottom: 28%;
      left: 50%; } }

  .block-wrap {
    width: 75%;
    height: 275px; }

  .block-col {
    width: 74px; }

  .block {
    height: 74px; }

  .block-img {
    margin: 10% auto auto;
    height: 58px;
    width: 58px; }

  .block-content__header__text {
    font-size: 1.5em; }

  .block-content__body {
    font-size: 14px;
    line-height: 1.333;
    animation: content-in 0.245s cubic-bezier(0, 0, 0.2, 1) forwards;
    animation-delay: 0.1s;
    margin: 0 0 10px; }

  .block-content {
    background-color: #336B87; }

  .block-content__button {
    left: 25%; } }
